<template>
  <!-- 审核弹窗 -->
  <el-dialog
    :close-on-click-modal="false"
    :show-close="false"
    :visible.sync="dialogVisible.trunoff"
    width="40%"
  >
    <div class="title_examine">
      {{title}}
      <img @click="colseFn" src="../../../assets/icons/esc.png" />
    </div>
    <el-form ref="form" class="examine" :model="form">
      <el-form-item>
        <el-radio-group v-model="form.checkStatus">
            <template v-if="title=='审核'">
                <el-radio label="通过"></el-radio>
                <el-radio label="拒绝"></el-radio>
            </template>
            <template v-else>
                <el-radio label="退成交"></el-radio>
                <el-radio label="退业绩"></el-radio>
            </template>
        </el-radio-group>
      </el-form-item>
      <el-form-item  v-if="title=='审核'" label="营收:">
        <el-input  v-model="form.revenue"></el-input>
      </el-form-item>
      <el-form-item label="原因:">
        <el-input type="textarea" v-model="form.reason"></el-input>
      </el-form-item>
      <el-form-item >
        <el-button style="background-color: #0b8d7e;color:#fff;" @click="onSubmit">提交</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>
<script>

import { checkAchieveTicket } from "@/api/business/Picturepreview";
export default {
  props: {
    dialogVisible: {
      type: Object
    },
    title:{
        type:String
    }
  },
  data() {
    return {
      form: {
        checkStatus: "",
        reason: "",
        ticketId:this.$store.getters.ticlkdeil.ticketId,
        revenue:(this.$store.getters.ticlkdeil.revenue).toFixed(2)
      },
      opty:{
          '通过':3,
          '拒绝':4,
          '退成交':5,
          '退业绩':6
      },
      optydei:{
          3:'通过',
          4:'拒绝',
          5:'退成交',
          6:'退业绩'
      }
    };
  },
  methods: {
    onSubmit() {
      switch (this.title) {
          case '审核':
              this.form.checkStatus=this.opty[this.form.checkStatus]
              checkAchieveTicket(this.form).then(res=>{
                   if(res.data.code==='000'){
                       this.$message.success('审核'+this.optydei[this.form.checkStatus]+'成功')
                       this.colseFn()
                   }
              })
              break;
          case '退定':
            //   delete this.form.revenue
              this.form.checkStatus=this.opty[this.form.checkStatus]
              checkAchieveTicket(this.form).then(res=>{
                   if(res.data.code==='000'){
                       this.$message.success(this.optydei[this.form.checkStatus]+'成功')
                       this.colseFn()
                   }else{
                       this.$message.error(res.data.msg)
                       this.colseFn()
                   }
              })
              break;
          default:
              break;
      }
    },
    colseFn() {
      this.$parent.$parent.onSubmit();
      this.$parent.$parent.handleClick(this.$store.getters.tickta);
      this.dialogVisible.trunoff = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.examine {
  .el-form-item:nth-child(1),
  .el-form-item:nth-child(3),
  .el-form-item:nth-child(4) {
    margin-bottom: 0px;
    text-align: center;
  }
}

/deep/.el-dialog {
  position: relative;
  border-radius: 6px;
  .el-dialog__header {
    padding: 0;
  }

  .title_examine {
    text-align: center;
    width: 100%;
    font-size: 24px;
    color: #000;
    img {
      position: absolute;
      top: 0;
      right: 10px;
    }
  }
  .el-dialog__body {
    padding: 10px 30px;
  }
}
</style>