            {
              title: "门店前佣",
              text: "",
              key: "preCommission",
              lable: "元"
            },
            {
              title: "门店后佣",
              text: "",
              key: "sufCommission",
              lable: "元"
            },
            {
              title: "门店现金奖",
              text: "",
              key: "cash",
              lable: "元"
            },
            {
              title: "门店总佣金",
              text: "",
              // key: "commSumMoney",sumCommission
              key: "sumCommission",
              lable: "元"
            },
            {
              title: "门店已结",
              text: "",
              key: "hasCommission",
              lable: "元"
            },
            {
              title: "营收",
              text: "",
              key: "revenue",
              lable: "元"
            },
            {
              title: "业绩备注",
              text: "",
              // key: "remark"achiRemark
              key: "achiRemark"
            }
          ],
          two: [
            {
              title: "业绩凭证",
              text: "",
              key: "picUrls2"
            },
            {
              title: "创建人",
              text: "",
              key: "createrName2"
            },
            {
              title: "创建时间",
              text: "",
              key: "createTime2"
            },
            {
              title: "状态",
              text: "",
              key: "checkStatus2"
            },
            {
              title: "提交时间",
              text: "",
              key: "commitTime2"
            },
            {
              title: "审核人",
              text: "",
              key: "checkName2"
            },
            {
              title: "审核时间",
              text: "",
              key: "checkTime2"
            },
            {
              title: "原因",
              text: "",
              key: "reason2"
            }
          ]
        }
      ],
      //图片预览
      PicviewOff: {
        trunoff: false,
        urlSrc: null
      },
      //审核弹窗
      examineOff: {
        trunoff: false
      },
      //退订弹窗
      examineOffs: {
        trunoff: false
      },
      // 业绩录入弹窗
      addchieveAlertOff: {
        trunoff: false
      },
      //业绩审核弹窗
      PerauditOff:{
        trunoff:false
      },
      //业绩退定弹窗
      PerauditOffs:{
        trunoff:false
      },
      //业绩编辑弹窗
      editchieveAlertOff:{
        trunoff:false
      }
    };
  },
  computed: {
    arrFn() {
      //  let data = [];
      // this.arr.forEach(item => {
      //   data = [...data, ...item.one, ...item.two];
      // });

      // data.forEach(item => {
      //   if (item.lable != undefined) {
      //     item.text =
      //       this.infoSuObj[item.key] == null ||
      //       this.infoSuObj[item.key] == undefined
      //         ? 0 + item.lable
      //         : this.infoSuObj[item.key] + item.lable;
      //   } else {
      //     if (item.key.indexOf("Time") != -1) {
      //       item.text = dateTime(this.infoSuObj[item.key]);
      //     } else if (item.title === "经纪人") {
      //       item.text = this.infoSuObj[item.key] + this.infoSuObj.agentPhone;
      //     } else {
      //       item.text = this.infoSuObj[item.key];
      //     }
      //   }
      // });
      this.conpoent(this.arr)
      return this.arr;
    },
    //成交
    arrsFn() {
      return this.conpoent(this.arrs);
    },
    //业绩
    arYe() {
      return this.conpoent(this.arrr);
    }
  },
  methods: {
    // 公共数据转换
    conpoent(arrval) {
      let data = [];
      arrval.forEach(item => {
        data = [...data, ...item.one, ...item.two];
      });
      
      data.forEach(item => {
        if (item.lable != undefined) {
          item.text =
            this.infoSuObj[item.key] == null ||
            this.infoSuObj[item.key] == undefined
              ? 0 + item.lable
              : this.infoSuObj[item.key] + item.lable;
        } else {
          if (item.key.indexOf("Time") != -1) {
            item.text = dateTime(this.infoSuObj[item.key]);
          } else if (item.title == "客户电话") {
            item.text =
              this.infoSuObj.preMobile +
              this.infoSuObj.midMobile +
              this.infoSuObj.sufMobile;
          }else if(item.title==='状态'){
            if(arrval[0].title==='认购单详细'){
              item.text=typeStatus(this.infoSuObj.checkStatus.toString())
            }else{
               item.text=typeStatus(this.infoSuObj.checkStatus.toString()+this.infoSuObj.checkStatus2)
            }
          }
          else {
            item.text = this.infoSuObj[item.key];
          }
        }
      });
      return arrval;
    },
    //编辑
    EidtPic(val) {
      switch (val) {
        case 1:
          this.$nextTick(() => {
            this.$emit("EidtPic");
          });
          break;
        case 2:
          this.editchieveAlertOff.trunoff=true
          break;
        default:
          break;
      }
      // this.$emit('EidtPic')
    },
    //提交
    examineFn(val) {
      switch (val) {
        case 1:
          this.$confirm("确认提交, 是否继续?", "提示", {
            confirmButtonText: "是",
            cancelButtonText: "否",
            type: "warning"
          }).then(() => {
            commitTicket({ ticketId: this.infoSuObj.ticketId }).then(res => {
              if (res.data.code === "000") {
                this.$parent.handleClick(this.infoSuObj);
                this.$parent.onSubmit();
                this.$message.success("提交成功");
              }
            });
          });
          break;
        case 2:
          this.$confirm("确认提交, 是否继续?", "提示", {
            confirmButtonText: "是",
            cancelButtonText: "否",
            type: "warning"
          }).then(() => {
            commitAchieveTicket({ ticketId: this.infoSuObj.ticketId }).then(
              res => {
                if (res.data.code === "000") {
                   this.$parent.handleClick(this.infoSuObj);
                this.$parent.onSubmit();
                  this.$message.success("提交业绩成功");
                }
              }
            );
          });

          break;

        default:
          break;
      }
    },
    //图片预览
    imgFn(url) {
      this.PicviewOff.trunoff = true;
      this.PicviewOff.urlSrc = url;
    },
    //审核////退订//添加
    Picturepreview(status) {
      switch (status) {
        case 1://成交审核
          this.examineOff.trunoff = true;
          break;
        case 2: //成交退定
          this.examineOffs.trunoff = true;
          break;
        case 3: //业绩添加
          this.addchieveAlertOff.trunoff = true;
          break;
        case 5: //业绩审核
          this.PerauditOff.trunoff=true
          break;
        case 6: //业绩退定
          this.PerauditOffs.trunoff=true
          break;
        default:
          break;
      }
    }
  }
};
</script>
